import { motion, useViewportScroll } from "framer-motion";
import { useQuery } from "react-query";
import styled from "styled-components";
import { makeImagePath } from "../../utils";
import { getMoviesDetail, IMovie } from "../api";

const BigMovie = styled(motion.div)`
  position: absolute;
  width: 40vw;
  height: 80vh;
  background-color: ${(props) => props.theme.black.lighter};
  left: 0;
  right: 0;
  margin: 0 auto;
  border-radius: 15px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
`;

const BigCover = styled.div`
  width: 100%;
  height: 400px;
  background-size: cover;
  background-position: center center;
`;
const BigWrap = styled.div`
  padding: 20px;
`;

const BigTitle = styled.h3`
  color: ${(props) => props.theme.white.lighter};
  position: relative;
  top: -60px;

  font-size: 24px;
`;

const BigInfo = styled.span``;

const Genres = styled.ul`
  display: flex;
`;

const Genre = styled.li`
  margin-right: 10px;
`;

const BigOverview = styled.p`
  color: ${(props) => props.theme.white.lighter};
`;

function Modal({ bigMovieMatch, clickedMovie }: any) {
  const { scrollY } = useViewportScroll();
  const {
    params: { movieId },
  } = bigMovieMatch;
  const { data, isLoading } = useQuery<IMovie>(["movies", "detail"], () =>
    getMoviesDetail(movieId)
  );
  console.log(data);
  return (
    <BigMovie
      layoutId={bigMovieMatch.params.movieId}
      style={{ top: scrollY.get() + 100 }}
    >
      {clickedMovie && (
        <>
          <BigCover
            style={{
              backgroundImage: `linear-gradient(to top,  black, transparent), url(${makeImagePath(
                clickedMovie.backdrop_path,
                "w500"
              )})`,
            }}
          />
          <BigWrap>
            <BigTitle>{clickedMovie.title}</BigTitle>
            <BigInfo>
              {data?.release_date.slice(0, 4)} / {data?.runtime}min
            </BigInfo>
            <Genres>
              {data?.genres?.map((genre) => (
                <Genre key={genre.id}>{genre.name}</Genre>
              ))}
            </Genres>
            <BigOverview>{clickedMovie.overview}</BigOverview>
          </BigWrap>
        </>
      )}
    </BigMovie>
  );
}

export default Modal;
